import React from 'react';
import Link from 'next/link';

const CrewTab = ({ crew }) => (
  <div className='crew--person'>
    <Link href='/person/[id]' as={`/person/${crew.id}`}>
      <a>{crew.name}</a>
    </Link>
    <small className='d-block mt-1'>{crew.department.join(', ')}</small>
  </div>
);

export default CrewTab;
